"use client";

export default function ChristmasHeader() {
  return (
    <header
      style={{
        position: "fixed",
        top: 0,
        left: 0,
        width: "100%",
        zIndex: 10000,
        background: "linear-gradient(135deg, #0a1628, #184274)",
        boxShadow: "0 2px 14px rgba(0,0,0,0.4)",
      }}
    >
      <nav
        style={{
          maxWidth: "1200px",
          margin: "0 auto",
          padding: "12px 20px",
          display: "flex",
          alignItems: "center",
          justifyContent: "space-between",
        }}
      >
        {/* LOGO */}
        <div style={{ display: "flex", alignItems: "center", gap: "10px" }}>
          <img
            src="/logo.png" // update if needed
            alt="Careerschool"
            style={{ height: "38px" }}
          />
          <span
            style={{
              color: "#ffcb0e",
              fontWeight: 700,
              fontSize: "14px",
              letterSpacing: "1px",
            }}
          >
            CHRISTMAS OFFER
          </span>
        </div>

        {/* MENU */}
        <div style={{ display: "flex", gap: "22px", alignItems: "center" }}>
          <a href="/" style={linkStyle}>Home</a>
          <a href="#programs" style={linkStyle}>Programs</a>
          <a href="#hubspot-form-container" style={ctaStyle}>
            Enroll Now
          </a>
        </div>
      </nav>
    </header>
  );
}

const linkStyle = {
  color: "#ffffff",
  textDecoration: "none",
  fontWeight: 600,
  fontSize: "14px",
};

const ctaStyle = {
  background: "linear-gradient(90deg, #ffcb0e, #ffa500)",
  color: "#2E477D",
  padding: "8px 16px",
  borderRadius: "999px",
  fontWeight: 700,
  fontSize: "14px",
  textDecoration: "none",
};
